import React, {useState} from 'react';
import styles from "./SearchPage.module.css"
import Header from "../Header/Header";
import {Input} from 'antd';
import {connect} from "react-redux";
import {onSearch, onSearchChanged, setTitleRequest} from "../../redux/search-reduser";
import VideoList from "./VideoList/VideoList";
import {HeartTwoTone} from '@ant-design/icons';
import {NavLink, Redirect, useHistory} from "react-router-dom";
import {setAuthUser} from "../../redux/auth-reduser";


const {Search} = Input;


const SearchPage = (props) => {

    const [message, setMessage] = useState('');

    const onSubmit = () => {
        props.setTitleRequest(message)
        props.onSearch(message)
    }

    return <div className={styles.wrapper}>
        <Header/>
        <div className={styles.searchWrapper}>
            <h1>Поиск видео</h1>
            <div className={styles.searchFormWrapper}>
                <form className={styles.search}>
                    <Search
                        placeholder="Что хотите посмотреть?"
                        allowClear
                        enterButton="Найти"
                        size="large"
                        onChange={(e) => setMessage(e.currentTarget.value)}
                        value={message}
                        onSearch={onSubmit}

                    />
                </form>
                <div className={styles.wrapperButton}>
                    <NavLink to="/add" className={styles.button}><HeartTwoTone style={{fontSize: '25px'}}/></NavLink>
                </div>
            </div>
        </div>
        <div className={styles.videoListWrapper}>
            <VideoList props={props.title}/>
        </div>
    </div>
}

const mapStateToProps = (state) => ({
    title: state.search.title,
    titleRequest: state.search.titleRequest,
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {onSearchChanged, setTitleRequest, onSearch, setAuthUser})(SearchPage);