import React from 'react';
import './App.css';
import 'antd/dist/antd.css';
import { Redirect, Route, Switch } from "react-router-dom";
import Login from "./components/Login/Login";
import "./App.css";
import SearchPage from "./components/SearchPage/SearchPage";
import SaveRequestForm from "./components/SaveRequestForm/SaveRequestForm";
import FavouritesPage from "./components/FavouritesPage/FavouritesPage";


const App = (props) => {
    return (
        <div className="App">
            <Route path='/' render={() => <Login />} />
            <Route
                path='/search'
                render={
                    () => {
                        if (localStorage.token) { return <SearchPage /> }
                        else { return <Redirect to="/" /> }
                    }
                }
            />
            <Route exact path='/add' render={() => <SaveRequestForm />} />
            <Route exact path='/favourites' render={() => <FavouritesPage />} />

        </div>
    );
}

export default App;

