const SET_AUTH_USER = 'SET_AUTH_USER';

let initialState = {
    isAuth: false
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_AUTH_USER:
            return {
                ...state,
                isAuth:action.isAuth
            }
        default:
            return state;
    }
}

export const setAuthUser = (isAuth) => ({
    type: SET_AUTH_USER,
    isAuth
})



export default authReducer;