import youtubeApi from "./../api/api"

const ON_SEARCH_CHANGED = 'ON_SEARCH_CHANGED';
const SET_STATE_DATA = 'SET_STATE_DATA';
const SET_TITLE_REQUEST = 'SET_TITLE_REQUEST';


let initialState = {
    titleRequest: "",
    videoMetaInfo: []

};

const searchReducer = (state = initialState, action) => {
    switch (action.type) {

        case ON_SEARCH_CHANGED:
            return {
                ...state,
                title: action.title
            }
        case SET_STATE_DATA:
            return {
                ...state,
                videoMetaInfo: action.data.videoMetaInfo

            }
        case SET_TITLE_REQUEST:
            return {
                ...state,
                titleRequest: action.titleRequest

            }


        default:
            return state;
    }
}

export const onSearchChanged = (title) => ({
    type: ON_SEARCH_CHANGED,
    title
})
export const setTitleRequest = (titleRequest) => ({
    type: SET_TITLE_REQUEST,
    titleRequest
})
export const setStateData = (data) => ({
    type: SET_STATE_DATA,
    data
})


export const onSearch = (keyword) => async (dispatch) => {
    const res = await youtubeApi.get("/search", {
        params: {
            q: keyword
        }
    })
    console.log(res)
    let data = {
        videoMetaInfo: res.data.items
    }
    dispatch(setStateData(data))
}

export default searchReducer;