import React from "react";
import {connect} from "react-redux";
import styles from "./Video.module.css"


function constructVideoTitles(data) {
    return data.map(({snippet, id}, index) => {
        return (
            <div
                className={styles.video}
                key={index}
            >
                <img src={snippet.thumbnails.high.url} alt="image" key={index} className={styles.img}/>
                <p>{snippet.title}</p>
            </div>
        );
    });
}

const Video = (props) => {
    return <>{constructVideoTitles(props.videoMetaInfo)}</>;
};

const mapStateToProps = (state) => ({
    videoMetaInfo: state.search.videoMetaInfo
})

export default connect(mapStateToProps,)(Video);

