import React from 'react';
import {Form, Input, Button} from 'antd';
import styles from './Login.module.css'
import logo from './../../assets/images/logo.svg'
import axios from "axios";
import {v4} from 'uuid';
import {Redirect} from "react-router-dom";
import {setAuthUser} from "../../redux/auth-reduser";
import {connect} from "react-redux";


const layout = {
    labelCol: {
        span: 8,
    },
    wrapperCol: {
        span: 16,
    },
};
const tailLayout = {
    wrapperCol: {
        offset: 8,
        span: 16,
    },
};

const Login = (props) => {
    if (localStorage.token) {
        return <Redirect to={"/search"}/>
    }

    const onFinish = (values) => {
        let name = values.username;
        let password = values.password;
        btnHandler(name, password);
    }
    if (props.isAuth) {
        return <Redirect to={"/search"}/>
    }

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const btnHandler = async (name, password) => {
        const res = await axios.get("test1.json")
        if (res.data.users.find(e => e.name === name && e.password === password)) {
            const key = v4();
            localStorage.setItem('token', key);
            props.setAuthUser(true);
        }

    }
    if (props.isAuth) {
        return <Redirect to={"/search"}/>
    }
    return (
        <div className={styles.mainWrapper}>
        <div className={styles.wrapperLoginForm}>
            <div className={styles.headline}>
                <div>
                    <img src={logo} alt="logo"/>
                </div>
                <div className={styles.headline__text}>
                    Вход
                </div>
            </div>
            <div className={styles.loginForm}>
                <Form
                    {...layout}
                    name="basic"

                    initialValues={{
                        remember: true,
                    }}
                    onFinish={onFinish}
                    onFinishFailed={onFinishFailed}
                >
                    <Form.Item
                        label="Логин"
                        name="username"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your username!',
                            },
                        ]}
                    >
                        <Input/>
                    </Form.Item>

                    <Form.Item
                        label="Пароль"
                        name="password"
                        rules={[
                            {
                                required: true,
                                message: 'Please input your password!',
                            },
                        ]}
                    >
                        <Input.Password/>
                    </Form.Item>
                    <Form.Item {...tailLayout}>
                        <Button type="primary" htmlType="submit">
                            Войти
                        </Button>
                    </Form.Item>
                </Form>
            </div>
        </div>
        </div>
    );
}

const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {setAuthUser})(Login);
